import { html, LitElement } from 'lit';
import { GLOBAL, CARDS, LANG, UTILS, GLOW } from '../../constants.js';
import { getMessage } from '../../utils.js';
import styles from './card.styles.js';

export class PersonCardV2 extends LitElement {

	_hass;

	static get properties() {
        return {
            _entity_name: { state: true },
            _entity_status: { state: true },
            _entity_picture: { state: true },
            _entity: { state: true }
        };
    }

	setConfig(config) {
		this._entityID = config.entity;
		this._charge_state_entity = config.charge_state_entity;
		this._charging_color = config.charging_color;
		this._icon_color = config.icon_color;
		this._icon = config.icon;
        if (this._hass) {
            this.hass = this._hass
        }
    }

	set hass(hass) {
        this._hass = hass;
        this._currentLanguage = LANG.getLanguage(hass.config.language);
		this._layout = this._config.layout || GLOBAL.layouts[0].value;
		this._entity = hass.states[this._entityID];
		if (!this._entity) {
			this._entity = null;
			return;
		}
		this._entity_status = this._entity.state;
		this._entity_name = this._entity.attributes.friendly_name;
		this._entity_picture = this._entity.attributes.entity_picture;
    }

	static styles = styles;

	render() {
		var content = html``;
		if (!this._entityID || !this._entity) {
			content = html`
				<ha-alert alert="error" style="display:block;">${getMessage('ENTITY_ERROR', this._currentLanguage)}</ha-alert>
			`;
		} else {
			this._entity_name = this._entity.attributes.friendly_name;
			this._entity_picture = this._entity.attributes.entity_picture;
			this._entity_status = this._entity.state;
			content = html`
				<div class="${(this._layout === GLOBAL.layouts[1].value) ? "container-column" : "container"}" @click="${this.openMoreInfo}">
					<div class="left">
						${
							(this._entity_picture == null) ? html`
								<div class="shape"></div>
								<ha-icon class="icon"></ha-icon>
							` : html`
								<img src="${this._entity_picture}" class="avatar" alt="avatar"></img>
							`
						}
						<div class="badge">
							<ha-icon class="badge-icon"></ha-icon>
						</div>
					</div>
					<div class="${(this._layout === GLOBAL.layouts[1].value) ? "right-column" : "right"}">
						<div class="name">${this._name || this._entity_name}</div>
						<div class="status">${this._entity_status || getMessage('UNAVAILABLE', this._currentLanguage)}</div>
					</div>
				</div>
			`;
		}
		var glow = false;
		if (this._charge_state_entity && (this._charge_state_entity.state === 'charging' || this._charge_state_entity.state === 'Charging')) {
			glow = true;
			const glow_color = this._charging_color || GLOW.colors.charging;
			this.style.setProperty('--glow-color', glow_color);
		}
		return html `
			<ha-card class="${(glow) ? 'glow' : 'glow-off'}">
				${content}
			</ha-card>
		`;
	}

	_fire(type, detail, options) {
		const event = new Event(type, {
			bubbles: options?.bubbles || true,
			cancelable: options?.cancelable || true,
			composed: options?.composed || true,
		});
		event.detail = detail || {};
		this.dispatchEvent(event);
	}

	openMoreInfo(event) {
		if (this._entityID) {
			this._fire('hass-more-info', { entityId: this._entityID });
		}
    }

	static getConfigElement() {
        return document.createElement(`${CARDS.personCardv2.type}-editor`);
    }

	static getStubConfig() {
        return {
            entity: "person.me",
        };
    }
}